import { Box, Canvas, LinearGradient, rect, vec } from '@shopify/react-native-skia';
import React from 'react';
import { View } from 'react-native';
import Icon from 'react-native-vector-icons/MaterialCommunityIcons';
import useTheme from '../hooks/useTheme';
import { CustomPressable } from './buttons/CustomPressable';

const SIZE = 50;

export default function CloseButton({ onClose }: { onClose: () => void }) {
  const theme = useTheme();
  const originPoint = SIZE * 4;

  return (
    <View pointerEvents="box-none">
      <Canvas
        style={{
          height: originPoint,
          width: originPoint,
          //backgroundColor: 'tomato',
        }}
        pointerEvents="none"
      >
        <LinearGradient
          start={vec(originPoint, 0)}
          end={vec(originPoint / 2, originPoint / 2)}
          colors={['rgba(0, 0, 0, 0.7)', 'rgba(0, 0, 0, 0.4)', 'rgba(0, 0, 0, 0.1)', 'rgba(0, 0, 0, 0)']}
        />
        <Box box={rect(0, 0, originPoint, originPoint)} />
      </Canvas>
      <CustomPressable
        onPress={onClose}
        style={{ position: 'absolute', right: theme.space.s, top: theme.space.s }}
      >
        <Icon name="close" size={32} color="white" />
      </CustomPressable>
    </View>
  );
}
