import React, { Fragment, PropsWithChildren } from 'react';
import { View } from 'react-native';
import useDeviceSize, { DEVICE_SIZES } from '../hooks/useDeviceSize';
import useTheme from '../hooks/useTheme';
import ProjectCard from './ProjectCard';
import ProjectsContainer from './ProjectsContainer';
import Text from './Text';

export default function ProjectsSection({
  projects,
  name,
}: PropsWithChildren<{ projects: Project[]; name: string }>) {
  const theme = useTheme();
  const { size: deviceSize, width } = useDeviceSize();
  const mobileView = deviceSize < DEVICE_SIZES.m;

  let renderProjects = projects;

  if (renderProjects.length % 2 === 1) {
    renderProjects = [
      ...renderProjects,
      { title: 'empty', description: 'empty', appIconSource: { uri: '' } },
    ];
  }

  return (
    <View
      style={{
        marginBottom: theme.space.xl * 2,
      }}
    >
      <Text
        style={{
          color: 'white',
          fontFamily: 'Futura Md BT',
          fontSize: 20,
          marginBottom: theme.space.m,
          fontWeight: 'bold',
        }}
      >
        {name}
      </Text>
      <ProjectsContainer>
        {renderProjects.map((p) => (
          <Fragment key={p.title}>
            {p.title === 'empty' ? (
              <View
                style={{
                  flex: width < 800 ? undefined : width < 1200 ? '33%' : '25%',
                }}
              />
            ) : (
              <ProjectCard key={p.title} p={p} theme={theme} />
            )}
          </Fragment>
        ))}
      </ProjectsContainer>
    </View>
  );
}
