import { Box, Canvas, rect, rrect } from '@shopify/react-native-skia';
import React from 'react';
import { View, ImageSourcePropType, ScrollView, Image } from 'react-native';
import useDeviceSize from '../hooks/useDeviceSize';
import useTheme from '../hooks/useTheme';
import { CustomPressable } from './buttons/CustomPressable';
import Text from './Text';

interface Props {
  images: ImageSourcePropType[];
  frameWidth?: number;
  landscape?: boolean;
}

const SHOW_SCROLL_CONTROLS = false;
const DEFAULT_WIDTH = 400;
const ASPECT_RATIO = 2;

export default function PhoneFrame({ images, frameWidth = DEFAULT_WIDTH, landscape = false }: Props) {
  const { width: screenWidth } = useDeviceSize();

  const theme = useTheme();
  const maxWidth = screenWidth - theme.space.m * 4;
  let width = Math.min(frameWidth, maxWidth);
  let height = width * ASPECT_RATIO;

  if (landscape) {
    width = Math.min(height, maxWidth);
    height = width / ASPECT_RATIO;
  }

  const valueToDetermineStrokesAndRadius = landscape ? height : width;

  const radius = valueToDetermineStrokesAndRadius * 0.14;
  const innerRadius = radius * 0.65;
  const innerStrokeWidth = valueToDetermineStrokesAndRadius * 0.03;
  const outerStrokeWidth = valueToDetermineStrokesAndRadius * 0.02;

  const space = theme.space.m;

  const innerWidth = width - space * 2 - innerStrokeWidth * 2 - outerStrokeWidth;
  const innerHeight = height - space * 2 - innerStrokeWidth * 2 - outerStrokeWidth;
  const innerLeft = space + innerStrokeWidth + outerStrokeWidth * 0.5;
  const innerTop = space + innerStrokeWidth + outerStrokeWidth * 0.5;

  return (
    <View style={{ width, height }}>
      <Canvas style={{ flex: 1 }}>
        <Box
          box={rrect(rect(space, space, width - space * 2, height - space * 2), radius, radius)}
          color="#666"
          style="stroke"
          strokeWidth={outerStrokeWidth}
        ></Box>
        <Box
          box={rrect(
            rect(
              space + outerStrokeWidth + 2,
              space + outerStrokeWidth + 2,
              innerWidth + innerStrokeWidth,
              innerHeight + innerStrokeWidth
            ),
            radius * 0.8,
            radius * 0.8
          )}
          color="black"
          style="stroke"
          strokeWidth={innerStrokeWidth}
        ></Box>
      </Canvas>

      {SHOW_SCROLL_CONTROLS && (
        <CustomPressable
          onPress={() => {
            //alert('todo');
          }}
          style={{
            position: 'absolute',
            left: -theme.space.xxl,
            top: height / 2 - 40,
          }}
        >
          <Text style={{ fontSize: 60, fontWeight: 'bold' }}>{'<'}</Text>
        </CustomPressable>
      )}
      <View
        style={{
          position: 'absolute',
          width: innerWidth,
          height: innerHeight,
          left: innerLeft,
          top: innerTop,
          borderRadius: innerRadius,
          overflow: 'hidden',
        }}
      >
        {images && (
          <View
            style={{
              borderRadius: innerRadius,
              overflow: 'hidden',
            }}
          >
            <ScrollView horizontal pagingEnabled scrollEnabled={images.length > 1}>
              {images.map((source, index) => (
                <Image
                  key={index}
                  source={source}
                  style={{
                    width: innerWidth,
                    height: innerHeight,
                  }}
                  resizeMode="cover"
                />
              ))}
            </ScrollView>
          </View>
        )}
      </View>

      {SHOW_SCROLL_CONTROLS && (
        <CustomPressable
          onPress={() => {
            //alert('todo');
          }}
          style={{
            position: 'absolute',
            right: -theme.space.xxl,
            top: height / 2 - 40,
          }}
        >
          <Text style={{ fontSize: 60, fontWeight: 'bold' }}>{'>'}</Text>
        </CustomPressable>
      )}
    </View>
  );
}
