import React from 'react';
import { useMemo } from 'react';
import type { SkiaValue } from '@shopify/react-native-skia';
import {
  useComputedValue,
  useLoop,
  BlurMask,
  vec,
  Canvas,
  Circle,
  Group,
  polar2Canvas,
  Easing,
  mix,
} from '@shopify/react-native-skia';

const c1 = '#61bea2';
const c2 = '#529ca0';

interface RingProps {
  index: number;
  progress: SkiaValue<number>;
}

const Ring = ({ index, progress, size }: RingProps) => {
  const R = size / 4;
  const center = useMemo(() => vec(size / 2, size / 2 - 64), [size]);

  const theta = (index * (2 * Math.PI)) / 6;
  const transform = useComputedValue(() => {
    const { x, y } = polar2Canvas({ theta, radius: progress.current * R }, { x: 0, y: 0 });
    const scale = mix(progress.current, 0.3, 1);
    return [{ translateX: x }, { translateY: y }, { scale }];
  }, [progress]);

  return (
    <Group origin={center} transform={transform}>
      <Circle c={center} r={R} color={index % 2 ? c1 : c2} />
    </Group>
  );
};

export const Breathe = () => {
  const size = 160;
  const slackFactor = 3;

  const center = useMemo(() => vec(size / 2, size / 2 - 64), [size]);

  const progress = useLoop({
    duration: 3800,
    easing: Easing.inOut(Easing.ease),
  });

  const transform = useComputedValue(() => [{ rotate: mix(progress.current, -Math.PI, 0) }], [progress]);

  return (
    <Canvas
      style={{
        width: size * slackFactor,
        height: size * slackFactor,
      }}
    >
      <Group
        transform={[
          { translateY: (size * slackFactor) / 2 - 24 },
          { translateX: (size * slackFactor) / 2 - 80 },
        ]}
      >
        <Group origin={center} transform={transform} blendMode="screen">
          <BlurMask style="solid" blur={40} />
          {new Array(6).fill(0).map((_, index) => {
            return <Ring key={index} index={index} progress={progress} size={size} />;
          })}
        </Group>
      </Group>
    </Canvas>
  );
};

// Export Breathe as default, we can lazy-load this way easier.
export default Breathe;
